@import '../../style/base.scss';

.homeContainer {
  composes: homeContainer from '../HomePage/HomePage.module.scss';
}

.homeColumn {
  composes: homeColumn from '../HomePage/HomePage.module.scss';
  justify-content: center;
}

.leftColumn {
  composes: leftColumn from '../HomePage/HomePage.module.scss';
}

.rightColumn {
  width: 100%;
  height: 100vh;
  min-width: calc(70% - 200px);
  /*  viewport  */
  position: sticky;
  /*  sticky */
  top: 0;
  /*  */
  background-color: $background-color;
}

.middleColumn {
  min-width: 30%;
  position: relative;
  background-color: $background-color;

  overflow-y: auto;

  // ()
  scrollbar-width: none;
  -ms-overflow-style: none; //ms

  &::-webkit-scrollbar {
    //scss
    display: none;
  }

}

// 
@media screen and (max-width: 426px) {
  .rightColumn {
    display: none;
  }

  .middleColumn {
    margin: unset;
    border-right: none;
    border-left: none;
  }
}